@import "variables.scss";

.advantage__wrap {
  position: relative;
  padding: 82px 20px 0 20px;
  display: flex;
  flex-direction: column;
  background: #f2f2f2;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: -2px;
    width: 100%;
    height: 100%;
    background: url("../img/bg-triangle-white.svg") no-repeat;
    background-size: 105%;

    @media (min-width: $tablet-width) {
      left: -1px;
      background-size: 130%;
    }

    @media (min-width: $desktop-width) {
      background-size: 157%;
    }
  }

  @media (min-width: $tablet-width) {
    flex-direction: row;
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;
    padding: 0 120px;
  }
}

.advantage__list {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    align-items: flex-start;
    padding: 10px 0 0 30px;
    width: 55%;
    order: 2;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    margin: -16px 0 0 0;
    padding: 0 20px;
    width: 100%;
  }
}

.advantage__item:not(:last-child) {
  padding-bottom: 15px;

  @media (min-width: $tablet-width) {
    padding-bottom: 19px;
  }
}

.advantage__item {
  @media (min-width: $desktop-width) {
    margin: 0 30px 0 0;
    width: 33%;
  }
}

.advantage__title {
  font-size: 18px;
  line-height: 24px;
  font-family: "Open Sans Bold", "Arial", sans-serif;
  text-align: center;
  color: #000000;

  @media (min-width: $tablet-width) {
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    font-size: 30px;
    line-height: 30px;
    text-align: center;
  }
}


.advantage__title--wold {
  @media (min-width: $desktop-width) {
    margin: 0 0 0 30px;
  }
}

.advantage__title--friends {
  @media (min-width: $desktop-width) {
    padding: 0 0 0 54px;
  }
}

.advantage__text {
  font-size: 14px;
  line-height: 24px;
  font-weight: normal;
  text-align: center;

  @media (min-width: $tablet-width) {
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    margin-top: 25px;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
  }
}

.advantage__text--wold {
  @media (min-width: $desktop-width) {
    margin: 25px 13px 0 38px;
  }
}

.advantage__text--friends {
  @media (min-width: $desktop-width) {
    padding: 0 0 0 42px;
  }
}

.advantage__pic {

  height: 300px;
  background: url("../img/iphone-screen-mobile@1x.png") no-repeat;
  background-position: center -129%;

  @media (min-width: $tablet-width) {
    width: 45%;
    height: 490px;
    margin-top: -64px;
    order: 1;
    background: url("../img/iphone-screen-3-tablet@1x.png") no-repeat;
    background-position: 0%;
    z-index: 2;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    background: url("../img/iphone-screen-1-desktop@1x.png") no-repeat, url("../img/iphone-screen-2-desktop@1x.png") no-repeat, url("../img/iphone-screen-3-desktop@1x.png") no-repeat;
    background-position: 2% 61%, 37% 61%, 72% 61%;
  }
}
